	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 22 – L’intervention (partie 2)","texte":{"childMarkdownRemark":{"html":"<p>Sam se réveille en premier.</p>\n<p>Sam : On… on est où?<br>\nWamien : Dans un fourgon. Le SIAM nous a capturés.<br>\nSam : Le SIAM? C’est quoi ça?<br>\nWamien : Aucune idée, j’ai entendu un garde parler avec quelqu’un au téléphone, il avait l’air de dire que c’était le nom du groupe qui nous détient. J’ai cru comprendre que Darakei Drumel n’est au courant de rien.</p>\n<p>Sam regarde autour de lui pendant que je lui parle.</p>\n<p>Sam : Ils sont où les autres?<br>\nWamien : Sans doute dans les autres fourgons, mais j’espère qu’ils vont bien.<br>\nSam : Louann se réveille.<br>\nLouann : Hm? J’ai entendu mon nom…</p>\n<p>Elle lève difficilement la tête et nous regardes, elle regarde autour d’elle.</p>\n<p>Louann : On est dans quoi là?<br>\nWamien : Un fourgon, je t’explique quand Marie est réveillée.<br>\nLouann : Hm, d’accord, Marie, la marmotte, réveille-toi!</p>\n<p>Marie ne réagit pas, sérieux elle dort h24 celle-là.</p>\n<p>Louann : Marie!!!</p>\n<p>Un garde cogne dans le devant.</p>\n<p>Garde 1 : Hé oh! Fermez-la!<br>\nMarie : Hein? Il se passe quoi?</p>\n<p>Elle nous regarde interrogative.</p>\n<p>Wamien : On est dans un fourgon, un groupe sous le nom du SIAM nous a attaqué et nous a capturés.<br>\nMarie : Le SIAM, j’en ai entendu parler. C’est le Service d’intervention anti-mutant. Ils ont essayé de venir chez moi, ensuite les Black Eagles sont venus et ensuite plus rien.</p>\n<p>Le véhicule s’arrête et on entend des gardes descendre du véhicule et aller vers l’arrière du fourgon. Les portes s’ouvrent et des gardes entrent dans le fourgon pour nous sortir de là, ils avaient sorti une chaise pour moi puisque j’ai encore mon masque à gaz, je ne peux pas du tout bouger. Ils nous font entrer dans un bâtiment, on est dans un stationnement vu l’odeur. Les gardes font avancer mes amis, nous sommes tous là, tous les 19. Les murs sont d’un blanc pur.</p>\n<p>Tim : J’ai hâte de tacher tous ses murs de sang!</p>\n<p>Un garde lui tape dans le dos.</p>\n<p>Garde 1 : Ferme-la! Tu ne vas rien faire le gringalet.<br>\nTim : Oh si tu savais, si je n’avais pas ce truc sur la bouche.</p>\n<p>Je pense que je peux résister à ça, c’est juste du gaz, si je retiens ma respiration assez longtemps, le gaz va arrêter de faire effet, mais s’il dure trop longtemps, ils vont s’en rendre compte. Autant essayer. Je commence à retenir ma respiration. Au bout de quelques secondes, je sens mes bras et mes jambes qui se réactivent, je recommence à respirer lentement. Ils ne se sont rendu compte de rien. Ils nous font tous nous asseoir sur des chaises et nous menottes comme on l’était dans le fourgon.</p>\n<p>Un homme entre dans la pièce un peu après que les gardes nous aient tous attachés.</p>\n<p>Homme : Bonjour à vous tous, désolé de vous avoir fait réveiller aussi tard dans la nuit. Je me présente Bruno Maes, président du SIAM, vous êtes ici parce que vous êtes des menaces. Vos gènes ont été activés trop tôt pour votre organisme.<br>\nWamien : Des menaces? Et puis on maîtrise nos pouvoirs.<br>\nBruno : C’est justement là le problème. Peu importe si vous avez le contrôle ou pas de vos pouvoirs. Vous êtes des erreurs de la nature…<br>\nHiro : Erreur toi-même! P’tit con!</p>\n<p>Bruno sourit, il semblait aimer ce genre de provocation.</p>\n<p>Bruno : On se calme jeune homme, donc je disais que vous êtes des erreurs de la nature, mais! Vous avez une chance de pouvoir vous reprendre et vivre en paix.<br>\nWamien : Quelle est-elle?<br>\nBruno : Et bien…<br>\nHiro : Ne l’écoute pas! Il va nous utiliser comme des esclaves!</p>\n<p>Tout le monde regarde Hiro.</p>\n<p>Wamien : Est-ce vrai monsieur Maes?<br>\nBruno : Bien sûr que non, c’est ridicule voyons.</p>\n<p>Je retiens ma respiration le temps que mon corps recommence à m’obéir et désactive mon masque, le gaz arrête de sortir et je recommence à respirer. Je suis prêt à attaquer si jamais il y a une occasion.</p>\n<p>Un homme entre avec un téléphone en main, il le tend à Bruno.</p>\n<p>Bruno : Ouais? Non, pas la peine de venir. Mes gardes ne vous laisseront pas entrer dans le complexe. Comment vous dites, vous êtes déjà là?</p>\n<p>Je me concentre et écoute ce que le téléphone dit. « Un agent spécial est déjà dans votre complexe. Il a pour objectif de faire sortir Wamien et son groupe. Vous avez donc deux options, soit vous les laissez sortir sans problème, sinon je m’organise pour que votre groupe n’ait plus aucun droit sur le royaume et les environs. »</p>\n<p>Bruno : Vous n’avez pas d’influence sur les lois du royaume, monsieur Drumel.</p>\n<p>La plupart de mes amis, surtout ceux qui ont été emprisonnés, se retournent vers moi.</p>\n<p>Darakei : Vous oubliez le poste que ma femme occupe?<br>\nBruno : Oh, je heum, non je le sais. Je les fais sortir.<br>\nDarakei : Parfait, comme quoi on peut toujours trouver un point d’entente.</p>\n<p>La ligne se coupe, Darakei a raccroché. Bruno se tourne vers nous et ensuite regarde un garde qui semble être le chef.</p>\n<p>Bruno : Fermez toutes les portes de cette pièce et vérifiez l’identité de tous ceux qui veulent entrer.<br>\nGarde : Compris monsieur!</p>\n<p>Il fait des signes aux gardes qui se placent aux diverses portes pour aller changer le mode de verrouillage. J’ai confiance en l’agent qui est ici.</p>"}},"chapitreAvant":"chapitre-22-l-intervention-partie-1","chapitreApres":"chapitre-23-le-pouvoir","nomRoman":"../le-premier-cyborg-tome-1","slug":"chapitre-22-l-intervention-partie-2"}},"pathContext":{"slug":"chapitre-22-l-intervention-partie-2"}}

